<!--
 * @Description: 分页表格
 * @Author: 徐强
 * @Date: 2021-08-11 14:03:27
-->
<template>
  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <screen title="分页表格" :columns="screenColumns" @search="search" />
      <actionButtons :columns="actionbtn" @customClick="customClick" />
      <el-table ref="theTable" class="table-sty" :data="tableData" style="width: 100%;margin-bottom: 20px;" row-key="id" :tree-props="{children: 'childMenuList'}">
        <el-table-column type="selection" width="55" align="center" />
        <!-- 想要设置展开行的列的前面的所有没有type的设置一个type  <el-table-column prop="menuName" type label="菜单名称" sortable width="180" /> -->
        <el-table-column prop="menuName" label="菜单名称" align="center" width="150" show-overflow-tooltip />
        <el-table-column prop="route" label="菜单路由" align="center" show-overflow-tooltip />
        <el-table-column prop="menuIcon" label="菜单图标" align="center" width="100" show-overflow-tooltip>
          <template slot-scope="{row}">
            <div v-if="!row.parentMenuId">
              <svg-icon :icon-class="row.menuIcon" style="font-size:28px" />
              {{ row.menuIcon }}
            </div>
            <!-- <svg-icon v-if="!row.parentMenuId" :icon-class="row.menuIcon" style="font-size:28px" /> -->
            <span v-else> ---- </span>
          </template>
        </el-table-column>
        <el-table-column prop="routeName" label="路由名称" align="center" min-width="150" show-overflow-tooltip />
        <el-table-column prop="routeType" label="路由类型" align="center" width="130" show-overflow-tooltip>
          <template slot-scope="{row}">
            <div>{{ row.routeType|routerType }}</div>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="routeType" label="是否启用" align="center" width="130" show-overflow-tooltip>
          <template slot-scope="scope">
            <el-switch v-model="scope.row.status" active-color="#13ce66" inactive-color="#ff4949" active-value="100" inactive-value="0" />
          </template>
        </el-table-column> -->
        <el-table-column prop="routeType" label="查看按钮" align="center" width="130">
          <template slot-scope="{row}">
            <span v-if="!row.childMenuList||(row.childMenuList&&row.childMenuList.length<1)" style="color:#1989fa" @click="seeBtn(row)"> 查看</span>
            <span v-else> ---- </span>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="menuName" label="菜单描述" align="center" /> -->
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="{row}">
            <el-button v-if="!row.pid" type="text" size="small" @click="addData('addchild',row)">添加下级</el-button>
            <el-button type="text" size="small" @click="editData(row)">修改</el-button>
            <el-button type="text" size="small" @click="delData(row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <page :pagination="pagination" @parent-method="getlist(false)" />
    </el-main>
  </el-container>
</template>
<script>
// import { PushPage } from "@/utils/message";
// getMenuIndex
// import { getPageTableIndex, } from "@/api/example";
import { getMenuIndex } from "@/api/functionPage.js";

export default {
  name: "AReplenishment",
  components: {
    page: () => import('@/components/BaseCom/page/index.vue'),
    // TableElement: () => import("@/components/BaseCom/elementTable/index"),
    actionButtons: () => import("@/components/BaseCom/actionButtons/index.vue"),
    screen: () => import("@/components/BaseCom/screen/index.vue")
  },
  filters: { // 过滤器
    routerType(item) { // 路由类型
      switch (item) {
        case 0:
          return "菜单路由";
        case 1:
          return "详情路由";
        default:
          return "数据异常";
      }
    }
  },
  data() {
    return {
      // 静态数据
      tableData: [],
      actionbtn: [],
      screenColumns: []
    };
  },
  mounted() {
    this.getlist()
    console.log(this.$route)
    // console.log(this.$route.meta.operationbtn)
    // console.log(this.$route.meta.tablebtn)
  },
  methods: {
    async getlist() {
      const result = await getMenuIndex({
        // page: this.pagination.currentPage,
        // pageSize: 10,
        // ...this.searchData
      });
      if (result.code === 200) {
        this.tableData = result.data
      }
      this.mainData = this.data
    },
    customClick(type) {
      this.$router.push({
        name: 'EqMaintenanceDetail',
        query: { type: 4 }
      })
    },
    radioChange(row) { // table单选 row为选中的数据集合
      // :show-radio="true"                  显示单选按钮
      // :select-radio.sync="selectRadio"    回显选中的行
      // radio-flag="id"                     行的唯一码  不传默认id
      //  @radioChange="radioChange"         获取选中行数据
      this.selectPickUp = row
    },
    handleSelectionChange(row) { // table多选框  row为选中的数据集合
      // :showcheckbox="true"                显示多选按钮
      //  @radioChange="radioChange"         获取选中行数据
      console.log(row)
    },
    // showIndex                     显示序号
    handleButton(btnType, row, index) { // btnType按钮唯一标识符 row当前行数据  index选中行下标
      if (btnType === 'file') {
        this.dialogState = { show: true, url: row.certification_img }
      } else {
        console.log(11)
      }
    },
    seeBtn() {
      this.$router.push({ path: '/functionPage/menu/btnlist', query: { id: '123456' }})
    },
    search(keyword) {
      this.searchData = keyword
      this.pagination.currentPage = 1
      this.mainData = [];
      this.getlist();
    },

    openDetail(row) {
      // PushPage(this, { name: "Agentdetail", params: row });
    },
    jumpPage(flag) {
      if (flag === 'add') {
        // PushPage(this, { name: "AgentEdit" });
      }
    },
  }
};
</script>
<style  lang="scss" scoped>
.specialform {
  ::v-deep .formitem {
    margin-bottom: 0px;
  }
}
</style>
