<!--
 * @Description: 生产商-产品信息详情
 * @Author: 编辑人
 * @Date: 2021-08-11 14:03:27
-->
<template>
  <el-container class="app-container">
    <el-main style="background: white;padding: 0px 20px;">
      <actionButtons :columns="actionbtn" title="商品详情" @customClick="customClick" />
      <el-table :data="rbacList" style="width: 100%;margin-bottom: 20px;" row-key="id" border :tree-props="{children: 'children',}">
        <el-table-column prop="menuName" label="菜单名称" width="180">
          <template slot-scope="scope">
            <el-checkbox v-model="scope.row.status" :true-label="1" :false-label="0" @change="handleCheckPageChange(scope.row.status,scope.row,scope.$index)">{{ scope.row.menuName }}</el-checkbox>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="按钮权限">
          <template slot-scope="{row}">
            <div v-if="row.children.length===0">
              <el-checkbox v-if="row.buttonList.length>0" :value="row.buttonList.every(item => item.status === 1)" :disabled="row.status===0" :indeterminate="!(row.buttonList.every((item, index) => item.status === 1) || row.buttonList.every((item, index) => item.status === 0))" @change="handleCheckAllChange(row.status,row)">全选</el-checkbox>
              <el-checkbox v-for="(item,i) in row.buttonList" :key="i" v-model="item.status" :true-label="1" :false-label="0" :disabled="row.status===0">{{ item.name }}</el-checkbox>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import { getRoleDetail } from "@/api/functionPage";
export default {
  name: "AShipInformation",
  components: {
    actionButtons: () => import("@/components/BaseCom/actionButtons"),

  },
  data() {
    return {
      rbacList: [],
      test: 1,
      imgList: [],
      productItem: [],
      checkedCities: [],
      indeterminate: false,
      actionbtn: [
        { label: "保存", icon: "icon-add", type: 'add', },
        { label: "返回", icon: "icon-del", type: 'del', },
      ],
      productItemInput: [{ pram: 'code', label: '产品编号', effect: 'form', rules: { required: true, type: "noChinese" }},
      { pram: 'name', label: '产品名称', effect: 'form', rules: { required: true }},
      { pram: 'tempclassification', label: '产品分类', effect: 'form', type: "cascader", rules: { select: true }, data: [], 'props': { value: 'id', label: 'name' }}, // 下拉框单选 data必须保持label  value格式数据
      { pram: 'rescondition', label: '贮存条件', effect: 'form', rules: { required: true }},
      { pram: 'trancondition', label: '运输条件', effect: 'form', rules: { required: true }},
      { pram: 'sales', label: '是否销售', effect: 'form', type: "select", rules: { select: true }, data: [{ label: '是', value: '1' }, { label: '否', value: '2' }] },
      { pram: 'unitprice', label: '产品价格', effect: 'form', rules: { required: true, type: "num" }},
      { pram: 'tempSpecs', label: '产品图片', effect: 'form', proport: 24 },
      { pram: 'tempSpec', label: '规格型号', effect: 'form', }],
      productItemReturn: [
        { pram: 'code', label: '产品编号', effect: 'form', rules: { required: true, type: "noChinese" }},
        { pram: 'name', label: '产品名称', effect: 'exhibit' },
        { pram: 'classificationMsg', label: '产品分类', effect: 'exhibit' }, // 下拉框单选 data必须保持label  value格式数据
        { pram: 'rescondition', label: '贮存条件', effect: 'exhibit', },
        { pram: 'trancondition', label: '运输条件', effect: 'exhibit', },
        { pram: 'salesMsg', label: '是否销售', effect: 'exhibit' },
        { pram: 'unitprice', label: '产品价格', effect: 'exhibit' },
        { pram: 'tempSpecs', label: '产品图片', effect: 'form', proport: 24 },
        { pram: 'tempSpec', label: '规格型号', effect: 'form', }
      ],
      // operation: [{ label: '新增', btnType: "add" }],
      productData: {},
      tempSpec: [{ id: "", specs: "", making: "" }],
      sign: "1"
    }
  },
  created() {
    // this.getProductClassData()
  },
  mounted() {
    this.getlist()
    // this.productItem = this.productItemInput
  },
  methods: {
    async getlist() {
      const result = await getRoleDetail({})
      if (result.code === 200) {
        this.rbacList = result.data
      }
      console.log(result)
    },
    customClick() {
      console.log(this.rbacList)
    },
    // value - id || city - parent_id
    getParent(data2, nodeId2) { // 通过子节点获取父节点 data2树形结构数据  当前节点id
      var arrRes = [];
      if (data2.length === 0) {
        if (nodeId2) {
          arrRes.unshift(data2)
        }
        return arrRes;
      }
      const rev = (data, nodeId) => {
        for (var i = 0, length = data.length; i < length; i++) {
          const node = data[i];
          if (node.id === nodeId) {
            arrRes.unshift(node)
            node.status = 1
            rev(data2, node.parentMenuId);
            break;
          } else {
            if (node.children) {
              rev(node.children, nodeId);
            }
          }
        }
        return arrRes;
      };
      arrRes = rev(data2, nodeId2);
      return arrRes;
    },
    handleCheckPageChange(val, row, i) { // 页面多选
      if (row.status === 1 && row.parentMenuId) {
         this.getParent(this.rbacList, row.id)
      }
      // const self = this
      // 当前checkbox状态
      const isChecked = row.status
      /**
       * 父级全选
       * @param {row: object}
       * 点击左侧checkbox 时 子级所有选项选中或取消 -- 包括右侧checkbox
      * */
      changeAll(row)
      function changeAll(rowData) {
        // rowData.indeterminate = false
        // 改变checkbox状态
        rowData.status = isChecked
        // 改变右侧表格中的checkbox
        rowData.buttonList &&
          rowData.buttonList.forEach(authItem => {
            authItem.status = isChecked
          })
        if (rowData.children) {
          rowData.children.forEach(element => {
            // 递归处理数据
            changeAll(element)
          })
        }
      }
    },
    handleCheckAllChange(val, row, status) {
      console.log(val, row, status)
      if (row.buttonList.every(item => item.status === 1)) {
        row.buttonList.forEach((element, i) => {
          row.buttonList[i].status = 0
        })
      } else {
        row.buttonList.forEach((element, i) => {
          row.buttonList[i].status = 1
        })
      }
    },

  }
};
</script>
<style  lang="scss" scoped>
</style>
