
export function sorts(ary, key) { // 将重构数据传递出去
  return sort(sort(ary, key), key);
}

export function AcquireMerge(data) {  //返回需要和并的数组
  let contactDot = 0;
  const spanArr = []
  data.forEach((item, index) => {
    if (index === 0) {
      spanArr.push(1)
    } else {
      if (item.label === data[index - 1].label) {
        spanArr[contactDot] += 1;
        spanArr.push(0)
      } else {
        contactDot = index
        spanArr.push(1)
      }
    }
  })
  return spanArr;
}
function sort(ary, key) {//重构数据  按照指定key进行分块排序
  return ary.reduce((total, next) => {
    const index = total.findIndex((item, index, self) => {
      return item[key] === next[key]
    })
    return index === -1 ? total.push(next) && total : total.splice(index, 0, next) && total
  }, [])
}
