<template>
  <el-container class="app-container">
    <el-main style="background: white;padding: 0px 20px;">
      <el-button @click="dateclick">aa</el-button>
      <actionButtons :columns="actionbtn" title="商品详情" @customClick="customClick" />
      <el-date-picker v-model="startDate" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd" :picker-options="pickerOptions('startDate',endDate)" placeholder="选择开始日期" />
      <el-date-picker v-model="endDate" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd" :picker-options="pickerOptions('endDate',startDate)" placeholder="选择结束日期" />
      {{ new Date(endDate).getTime() }}-      {{ new Date(startDate).getTime() }}
      <!-- {{ pickerOptions0 }}-{{ pickerOptions1 }}-{{ value1 }}-{{ value2 }}
      <el-date-picker v-model="value1" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd" placeholder="开始日期" :picker-options="pickerOptions1" />
      <el-date-picker v-model="value2" type="date" format="yyyy-MM-dd" value-format="yyyy-MM-dd" placeholder="结束日期" :picker-options="pickerOptions0" /> -->
