          <span style="color:red">*</span>规格型号
        </template>
        <template slot="tempSpecContent">
          <div v-for="(items,i) in tempSpec" :key="i" style="width: calc(81% + 80px);text-align:center;display:flex;margin-bottom:5px">
            <div style="width: calc(100% - 80px);display:flex;">
              <el-input v-model="items.specs" placeholder="规格" />
              <div style="width:40px"> - </div>
              <el-input v-model="items.making" placeholder="型号" />
            </div>
            <div style="width:80px;display:flex;align-items: center;">
              <i v-if="i>=tempSpec.length-1" class="el-icon-circle-plus-outline" style="padding-left:10px;font-size:20px;cursor: pointer;" @click="addspec()" />
              <i v-if="!items.id" class="el-icon-remove-outline" style="padding-left:10px;font-size:20px;cursor: pointer;" @click="delspec(i)" />
            </div>
          </div>
        </template>
        <template slot="tempSpecsContent">
          <uploadFile ref="uploadfile" :img-list.sync="imgList" />
        </template>
      </customForm>
      <div style="text-align: center;">
        <el-button type="primary" @click="subFormData">提交</el-button>
        <el-button type="primary">返回</el-button>
      </div>
    </el-main>
  </el-container>
</template>
