        // 下拉框 props(绑定展示参数) filterable(是否可搜索) clearable(是否可清空) multiple(是否多选) data下拉框数据源
        { pram: 'sales', label: '下拉框', effect: 'form', type: "select", rules: { select: true }, props: { value: 'value', label: 'label' }, filterable: true, clearable: true, multiple: false, data: [{ label: '是', value: '1' }, { label: '否', value: '2' }] },
