        // 密码框 clearable(是否可清空) disabled(是否禁用)
        { pram: 'name', label: '密码框', type: "possword", effect: 'form', clearable: true, disabled: false, rules: { required: true }},
        // 放大镜框 suffixIcon放大镜框后缀图标  clearable(是否可清空) disabled(是否禁用)
        { pram: 'name', label: '放大镜框', type: "choice", effect: 'form', suffixIcon: "el-icon-search", clearable: true, disabled: false, rules: { required: true }},
