        // 输入框 验证 rules.required(是否必填验证) rules.type(验证类型)  验证类型文件 @/assets/js/validate.js
        { pram: 'name', label: '正常输入', effect: 'form', rules: { required: true }}, // 输入框 必填验证
        // 使用插槽 proport分割比例(值为8三等分，值为12二等分 值为24独占一行)
        { pram: 'tempSpecs', label: '上传图片', effect: 'form', proport: 24 },
        // textarea文本 clearable(是否可清空)  disabled(是否禁用)  maxlength: 最大字符, autosize: { minRows: 最小行数, maxRows: 最大行数 },
        { pram: 'name', label: 'textarea', type: "textarea", effect: 'form', clearable: true, disabled: false, rules: { required: true }},
        /** 展示框 */
        // 正常数据
        { pram: 'conId', label: '合同编号' },
        // 操作按钮 btnType是按钮的标记判断点击按钮的位置
        { pram: '按钮名称', label: '操作', type: 'btn', btnType: "edit", effect: 'exhibit' },
        // 过滤展示 funName fillter里的过滤方法
        // { pram: 'nature', label: '生产性质', funName: "natureFilter", effect: 'exhibit' },
        // 拼接展示 spliceType表示通过/截取pram  使用、拼接展示
        { pram: 'specs/making', label: '拼接展示', type: 'splice', spliceType: "/,、", effect: 'exhibit' },

      ],
      imgList: [],
      // productItem: [],
      actionbtn: [
        { label: "保存", icon: "icon-add", type: 'add', },
        { label: "返回", icon: "icon-del", type: 'del', },
      ],
      productItemInput: [{ pram: 'code', label: '产品编号', effect: 'form', rules: { required: true, type: "noChinese" }},
      { pram: 'name', label: '产品名称', effect: 'form', rules: { required: true }},
      { pram: 'tempclassification', label: '产品分类', effect: 'form', type: "cascader", rules: { select: true }, data: [], 'props': { value: 'id', label: 'name' }}, // 下拉框单选 data必须保持label  value格式数据
      { pram: 'rescondition', label: '贮存条件', effect: 'form', rules: { required: true }},
      { pram: 'trancondition', label: '运输条件', effect: 'form', rules: { required: true }},
      { pram: 'sales', label: '是否销售', effect: 'form', type: "select", rules: { select: true }, data: [{ label: '是', value: '1' }, { label: '否', value: '2' }] },
      { pram: 'unitprice', label: '产品价格', effect: 'form', rules: { required: true, type: "num" }},
      { pram: 'tempSpecs', label: '产品图片', effect: 'form', proport: 24 },
      { pram: 'tempSpec', label: '规格型号', effect: 'form', }],
      productItemReturn: [
        { pram: 'code', label: '产品编号', effect: 'form', rules: { required: true, type: "noChinese" }},
        { pram: 'name', label: '产品名称', effect: 'exhibit' },
        { pram: 'classificationMsg', label: '产品分类', effect: 'exhibit' }, // 下拉框单选 data必须保持label  value格式数据
        { pram: 'rescondition', label: '贮存条件', effect: 'exhibit', },
        { pram: 'trancondition', label: '运输条件', effect: 'exhibit', },
        { pram: 'salesMsg', label: '是否销售', effect: 'exhibit' },
        { pram: 'unitprice', label: '产品价格', effect: 'exhibit' },
        { pram: 'tempSpecs', label: '产品图片', effect: 'form', proport: 24 },
        { pram: 'tempSpec', label: '规格型号', effect: 'form', }
      ],
      operation: [{ label: '新增', btnType: "add" }],
      productData: {},
      tempSpec: [{ id: "", specs: "", making: "" }],
      sign: "1"
    }
  },
  // watch: { // 监听父组件传过来的数据，动态更新
  //   echertData: {
  //     handler(newVal, oldVal) {
  //       this.Render()
  //     },
  //     deep: true,
  //     immediate: true,
  //   }
  // },
  created() {
    this.getProductClassData()
  },
  mounted() {
    // this.productItem = this.productItemInput
  },
  methods: {
    async subData(type, row) {
      if (this.tempSpec.length === 1 && JSON.stringify(this.tempSpec[0]) === "{ specs: '', making: '' }") {
        this.$message.error('请至少填写一个规格型号')
        return
      }
      if (this.imgList.length === 0) {
        this.$message.error('请上传至少一张产品图片')
        return
      }
      if (this.sign === "1") {
        this.submit(type, row)
      } else {
        this.addSamePro(type, row)
      }
    },
    async submit(type, row) {
      var templist = []
      if (this.judge('specs') && this.judge('making')) {
        this.tempSpec.forEach(element => {
          templist.push({
            ...row,
            picture: this.imgList.join(','),
            classification: row.tempclassification[row.tempclassification.length - 1],
            making: element.making,
            specs: element.specs,
          })
        });
        // const result = await addProductData(templist)
        // if (result.code === 200) {
        //   this.$messages('新增成功')
        //   PushPage(this, { name: "ProductInformation" }, true);
        // }
        // console.log(result)
      }
    },

    customClick() {

    },
    async addSamePro(type, row) {
      if (this.judge('specs') && this.judge('making')) {
        var templist = []
        this.tempSpec.forEach(element => {
          if (!element.id) {
            templist.push({
              "model": element.making,
              "spec": element.specs,
            })
          }
        });
        // const formData = {
        //   "imgUrl": this.imgList.join(','),
        //   "manufactureCode": this.$store.state.user.account,
        //   "productCode": this.productData.code,
        //   "specs": templist
        // }
        // const result = await addManuSameCodePro(formData)
        // if (result.code === 200) {
        //   this.$messages('新增成功')
        //   PushPage(this, { name: "ProductInformation" }, true);
        // }
        // console.log(result)
      }
    },
    pickerOptions(type, date) {
      if (type === "aftertoday") {
        // 今天和以后
        return { disabledDate(time) { return time.getTime() < Date.now() - 8.64e7 } }
      } else if (type === "beforetoday") {
        // 今天和以前
        return { disabledDate(time) { return time.getTime() > Date.now() - 8.64e6 } }
      } else if (type === "beforetoday") {
        // 明天和以后
        return { disabledDate(time) { return time.getTime() < Date.now() } }
      } else if (type === "beforetoday") {
        // 昨天和以前
        return { disabledDate(time) { return time.getTime() > Date.now() } }
      } else if (type === "60") {
        // 60天之前到现在
        return {
          disabledDate(time) {
            const curDate = (new Date()).getTime();
            const three = 90 * 24 * 3600 * 1000;
            const threeMonths = curDate - three;
            return time.getTime() > Date.now() || time.getTime() < threeMonths;
          }
        }
      } else if (type === "6") {
        // 60天之前到现在
        return {
          disabledDate(time) {
            const curDate = (new Date()).getTime();
            const three = 90 * 24 * 3600 * 1000;
            const threeMonths = curDate - three;
            //  return time.getTime() < this.value1 || time.getTime() > Date.now();
            return time.getTime() > Date.now() || time.getTime() < threeMonths;
          }
        }
      } else if (type === "startData") {
        // 60天之前到现在
        return {
          disabledDate: time => {
            if (date) { // 必须早于结束日期
              return (
                time.getTime() > new Date(date).getTime()
              )
            }
            // else { // 不能早于当前日期
            //   return time.getTime() > Date.now()
            // }
          }
        }
      } else if (type === "endDate") {
        return {
          disabledDate: time => { //  不能早于开始日期
            if (date) {
              return time.getTime() < new Date(date).getTime();
            }
          }
        }
      }
      // var validityPickerOptions = {};
      // if (type === 0) {
      //   validityPickerOptions = {
      //     disabledDate: time => {
      //       return time.getTime() < Date.now() - 8.64e7;
      //     }
      //   };
      // } else if (type === 1) {
      //   validityPickerOptions = {
      //     disabledDate: time => {
      //       return time.getTime() > Date.now();
      //     }
      //   };
      // }
      // return validityPickerOptions;
    },
    pickerOptions0() {
      if (this.value2 !== "") {
        return {
          disabledDate: time => {
            return time.getTime() > Date.now() || time.getTime() > Date.parse(new Date(this.value2));
          }
        };
      } else {
        return { disabledDate: time => { return time.getTime() > Date.now() } };
      }
    },
    pickerOptions1() {
      return {
        disabledDate: time => {
          return time.getTime() < Date.parse(new Date(this.value1)) || time.getTime() > Date.now();
        }
      };
    },
    startTime1() {
      return {
        disabledDate: time => {
          if (this.endDate) {
            // 必须早于结束日期
            return (
              time.getTime() > new Date(this.endDate).getTime()
            )
          } else {
            // 不能早于当前日期
            return time.getTime() > Date.now()
          }
        }
      }
    },
    endTime1() {
      return {
        disabledDate: time => {
          if (this.startDate) {
            //  不能早于开始日期
            return time.getTime() < new Date(this.startDate).getTime();
          }
        }
      }
    },
    dateclick() {
      // console.log('(new Date()).getTime() ', (new Date()).getTime())
      // console.log('this.value1', this.value1)
      // var stringTime = '2012-10-12 22:37:33';
      // 将获取到的时间转换成时间戳
      // var timestamp = Date.parse(new Date(this.value1));
      console.log(Date.now() > Date.parse(new Date(this.value2)), "Date.now()>Date.parse(new Date(this.value2))")
      console.log(Date.now(), "Date.now()")
      console.log(Date.parse(new Date(this.value1)), "value1")
      console.log(Date.parse(new Date(this.value2)), "value2")
    },
    async getProductDetail() {
      // const result = await getProductDetail({ code: 1 })
      // this.productData = {
      //   ...result.data,
      //   tempclassification: [result.data.pfid, result.data.pid],
      // }
      // this.tempSpec = []
      // result.data.list.forEach(element => {
      //   const temp = element.model.split('/')
      //   this.tempSpec.push({ specs: temp[0], making: temp[1] })
      // });
    },
    async getProductClassData() {
      // const result = await getProductClassData()
      // this.productItemInput[2].data = result.data
      // this.productItemReturn[2].data = result.data
    },
    subFormData() {
      if (this.$refs.research.submitForm()) { // 验证  如果验证通过

      }
    },

    addspec() {
      if (this.judge('specs') && this.judge('making')) {
        this.tempSpec.push({ specs: "", making: "" })
      }
    },
    delspec(i) {
      if (this.tempSpec.length < 2) {
        this.tempSpec = [{ specs: "", making: "" }]
      } else {
        this.tempSpec.splice(i, 1)
      }
    },
    judge(param) { // 判断规格型号是否一一对应
      for (let index = 0; index < this.tempSpec.length; index++) {
        if (this.tempSpec[index][param] === '') {
          this.$message.error('请填写第' + (index + 1) + '行的' + (param === 'specs' ? '规格' : '型号'))
          return false
        }
      }
      return true
    },
    itemChange(pram, value) { // 监听form表单item数据改变 pram为当前item绑定参数，value为当前item值
      console.log(pram, value)
    },
    getMsgByName(arr, key, value) {
      for (let i = 0; i < arr.length; i++) {
        var temp = arr[i][key];
        if (temp === value) {
          return arr[i].label;
        }
      }
      return false;
    },
    getMsgByNameSort(arr, value) {
      let res = ""
      for (let i = 0; i < arr.length; i++) {
        const obj = arr[i]
        if (obj.children && obj.children.length > 0) {
          res = this.getMsgByNameSort(obj.children, value)
        }
        if (obj.id === parseInt(value)) {
          res = obj.name
        }
      }
      return res;
    }
  }
};
</script>
