<template>
  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <div>设置商品图片主图</div>
      <upload-img :img-list.sync="imgList" :show-main-graph="false" :limit="5" />
      <upload-img :img-list.sync="imgList" :show-main-graph="true" :limit="5" />
      {{ imgList }}
      <div>上传图片限制</div>
      <uploadFile v-model="fileList" :show-main-graph="true" list-type="picture-card" upload-tips="提示" />
      <uploadFile v-model="fileList" list-type="text" upload-tips="提示" />
      <uploadFile v-model="fileList" list-type="picture" upload-tips="提示" @on-before-upload="beforeUpload" />

    </el-main>
  </el-container>
</template>
<script>
// import { goods } from "./mockStatic";
export default {
  name: "AReplenishment",
  components: {
    uploadImg: () => import('@/components/BaseCom/uploadImg/index'),
    uploadFile: () => import('@/components/BaseCom/uploadFile/index'),
    // uploadFile: () => import('@/components/BaseCom/uploadFile'),

  },
  data() {
    return {
      imgList: ['https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/cf752099-4ad3-44ab-bb8c-8b6ee5ae8064.jpg',
        'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/cfb3ec2f-187f-41a0-b8e6-eea3acce71ae.jpg',
        'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/511af6e3-6109-48b7-a609-789af2fba49c.jpg',
        'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/277419f0-3943-4b7a-be64-a513c9f77a80.jpg'],
      title: "",
      fileList: [{
        name: "文件名称",
        url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/cf752099-4ad3-44ab-bb8c-8b6ee5ae8064.jpg'
      }],
      dialogState: { show: false, url: '' }
    };
  },
  mounted() {

  },
  methods: {
    openPreview(i) {
      if (i === 1) {
        this.dialogState = { show: true, url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/0ff58062-0f82-476f-9d66-52d9e9bd7361.pdf' }
      } else if (i === 2) {
        this.dialogState = { show: true, url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/56ab1b98-fcd2-45c6-83a3-cdba8a830238.docx' }
      } else if (i === 3) {
        this.dialogState = { show: true, url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/607241ca-3083-4105-8776-8f34eee61e6a.doc' }
      } else {
        this.dialogState = { show: true, url: 'https://itree.i-ortho.cn/api/upload/20220705/微信图片_20220704151224.jpg' }
      }
    },
    beforeUpload(file, callback) { // 上传前的钩子函数 判断是否同意上传
      callback(true); // callback(true);和不写 callback()都表示 同意上传  callback(false);表示不同意上传
    },

  }
};
</script>
