<template>

  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <screen title="按钮管理" />
      <actionButtons :columns="actionbtn" @customClick="customClick" />
      <el-dialog title="新增按钮" :visible.sync="btndialogadd" width="30%" :show-close="false">
        <el-form :model="btnItemData" label-width="120px">
          <!-- label: '', // 操作名称
        btnPlace: '', // 按钮位置  1页面按钮  2表格按钮
        btnType: '', // 按钮类型   （文字按钮[textbtn]svg图标按钮[svgIcon]、icon图标按钮[icon]、）
        name: "", // 图标名称     按钮需要展示的标题  或者图标名称
        btnFlag: "", // 按钮标记  按钮唯一值
        remark: "", // 备注 -->
          <el-form-item label="按钮位置">
            <el-select v-model="btnItemData.btnPlace" placeholder="请选择路由类型" class="el-dialog_row_input" @change="btnPlaceChange">
              <el-option label="页面按钮" :value="1" />
              <el-option label="表格按钮" :value="2" />
            </el-select>
          </el-form-item>
          <el-form-item label="操作名称">
            <el-input v-model="btnItemData.label" :disabled="btnItemData.btnPlace===1" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item label="按钮类型">
            <el-select v-model="btnItemData.btnType" placeholder="请选择路由类型" class="el-dialog_row_input" :disabled="btnItemData.btnPlace===1" @change="$forceUpdate()">
              <el-option label="文字按钮" value="textbtn" />
              <el-option label="svg图标按钮" value="svgIcon" />
              <el-option label="icon图标按钮" value="icon" />
            </el-select>
          </el-form-item>
          <el-form-item :label="btnItemData.btnType==='textbtn'?'按钮名称':'图标名称'">
            <el-input v-model="btnItemData.name" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item v-if="btnItemData.btnPlace===1" label="按钮图标">
            <el-input v-model="btnItemData.operateIcon" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item label="按钮标记">
            <el-input v-model="btnItemData.operatorProperties" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="btnItemData.remark" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
        </el-form>
        <div style="text-align:center">
          <el-button type="primary" @click="subbtn">立即创建</el-button>
          <el-button @click="btndialogadd=false">取消</el-button>
        </div>
      </el-dialog>
      <el-table :data="tableData1" :span-method="objectSpanMethod" border style="width: 100%; margin-top: 20px">
        <el-table-column prop="label" label="操作名称" align="center" width="100" show-overflow-tooltip />
        <el-table-column label="按钮位置" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.btnPlace===1?'页面按钮':'表格按钮' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="按钮位置" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.btnType==='textbtn'?'文字按钮': scope.row.btnType==='svgIcon'?'svg按钮': 'icon按钮' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="按钮名称" align="center">
          <template slot-scope="{row}">
            <div>  <i v-if="row.operateIcon" :class="['icon', row.operateIcon.indexOf('el-icon')!==-1? row.operateIcon: ['iconfont' , row.operateIcon ? row.operateIcon :'']]" /> {{ row.name }} </div>
          </template>
        </el-table-column>
        <el-table-column prop="btnFlag" align="center" label="按钮标记" show-overflow-tooltip />
        <el-table-column label="是否启用" align="center">
          <template>
            <span>暂时没做</span>
          </template>
        </el-table-column>
        <el-table-column label="按钮请求地址" align="center">
          <template>
            <span>暂时没做</span>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="备注" show-overflow-tooltip />
        <!-- <el-table-column prop="remark" label="按钮状态" show-overflow-tooltip /> -->
        <el-table-column label="操作" width="150" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="operationBtn(scope.row,'edit')">修改</el-button>
            <el-button type="text" size="small" @click="operationBtn(scope.row.id,'del')">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import { getBtnIndex } from "@/api/functionPage.js";

import { sorts, AcquireMerge } from "@/assets/js/onsolidatedTable.js";

export default {
  components: {
    actionButtons: () => import("@/components/BaseCom/actionButtons"),
    screen: () => import("@/components/BaseCom/screen")
  },
  filters: {
    filterRouteType(item) {
      switch (item) {
        case 0:
          return "菜单目录";
        case 1:
          return "详情页面";
        default:
          return "数据异常";
      }
    },
  },
  data() {
    return {
      actionbtn: [
        { label: "新增", icon: "icon-add", type: 'add', },
        { label: "删除", icon: "icon-del", type: 'del', },
      ],
      // 按钮位置 btnPlace    1表格按钮  2页面按钮    改为：   2表格按钮  1页面按钮
      // 操作名称 tableHeader   表头展示   操作按钮不用存在
      // 按钮类型 btnType   按钮类型、（文字按钮[textbtn]svg图标按钮[svgIcon]、icon图标按钮[icon]、）
      // 图标名称 name     按钮需要展示的标题  或者图标名称
      // 操作图标 operateIcon     操作的图标
      // 按钮标记 btnFlag  按钮唯一值
      // 备注

      btnItemData1: {
        label: '', // 操作名称
        btnPlace: '', // 按钮位置  1页面按钮  2表格按钮
        btnType: '', // 按钮类型   （文字按钮[textbtn]svg图标按钮[svgIcon]、icon图标按钮[icon]、）
        name: "", // 图标名称     按钮需要展示的标题  或者图标名称
        btnFlag: "", // 按钮标记  按钮唯一值
        operateIcon: "", // 页面按钮图标
        remark: "", // 备注
      },
      btnItemData: {
        label: ''
      },
      tableData1: [],

      addType: "",
      tableData: [],
      dialogTableVisible: false,
      btndialog: false,
      btndialogadd: false,
      btnData: [],
      pageId: "",
      spanArr: []
    }
  },
  mounted() {
    this.view()
  },
  methods: {
    async view() {
      const result = await getBtnIndex()
      this.tableData = result.data
      this.tableData1 = sorts(this.tableData, 'label')// 将数据排序成相同value值放在一起的排序
      this.spanArr = AcquireMerge(this.tableData1)// 获取向下和并的数组
    },
    btnPlaceChange(value) { // 弹窗按钮位置切换事件
      if (value === 1) {
        this.btnItemData.label = "页面按钮"
        this.btnItemData.btnType = 'textbtn'
      } else {
        this.btnItemData.label = ""
        this.btnItemData.btnType = ""
      }
    },
    async operationBtn(row, type) {

    },
    customClick() {
      this.btndialogadd = true
    },
    async subbtn() {
      // const result = await createBtn(this.btnItemData, this.btnItemData.id ? 'put' : 'post')
      // console.log(result)
    },
    // 和并单元格
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) { // 需要和并的列
        if (this.spanArr[rowIndex]) {
          return {
            rowspan: this.spanArr[rowIndex],
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    },
  },

}
</script>
<style lang="scss" scoped>
.el-dialog_row_input {
  width: calc(100% - 120px);
}
.table-sty {
  height: 100%;
  ::v-deep .el-table__body-wrapper {
    height: calc(100% - 60px) !important;
    overflow-y: auto;
  }
  ::v-deep ::-webkit-scrollbar {
    /*滚动条里面小方块*/
    width: 3px;
    height: 8px;
  }
  ::v-deep ::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 5px;
    // -webkit-box-shadow: inset 0 0 5px rgba(1, 21, 48, 1);
    // box-shadow: inset 0 0 5px rgba(1, 21, 48, 1);
    background: #c1c8ce;
  }
  ::v-deep ::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    // -webkit-box-shadow: inset 0 0 5px rgba(14, 54, 94, 0.2);
    // box-shadow: inset 0 0 5px rgba(14, 54, 94, 0.2);
    border-radius: 5px;
    background: #fff;
  }
}
</style>
