<!--
 * @Description: 公用方法模板
 * @Author: 徐强
 * @Date: 2021-08-11 14:03:27
-->
<template>
  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <screen title="方法模板" />
      <el-button @click="unrepeated">数组对象去重</el-button>
      <el-button @click="filterData('isAcceptStatus')">过滤器字符串生成数据</el-button>
      <el-button @click="recursionFunction">递归方法</el-button>
      <el-button @click="createMockData">生成mock数据命令</el-button>
      <el-button @click="saveJsonFile">生成json文件并下载</el-button>
      <el-button @click="antiShake">防抖</el-button>
      <el-button @click="throttle">节流</el-button>
      <screen title="自定义指令" />
      <el-button v-debounce="[reset,`click`,1000]">按钮数组防抖</el-button>
      <el-input v-model="value" v-debounce="[reset,`input`,1000]" placeholder="输入框数组防抖" />
      <el-button v-btnDebounce:500 @click="reset">按钮防抖</el-button>
      <el-button v-btnThrottle @click="reset">按钮节流</el-button>
      <el-input v-model="value" v-inputDebounce:500="reset" placeholder="输入框简单防抖" />
      时间转换: <span v-relativeTime="1659329744959">1</span>
      <!-- <div>
        <el-button @click="getUserInfo">获取用户信息</el-button>
        <el-button @click="getUserInfo2">获取用户信息2</el-button>
      </div> -->
    </el-main>
  </el-container>
</template>
<script>
import { unrepeated, recursionFunction, createMockData, antiShake, saveMobility, saveJsonFile, filterData } from "@/assets/js/common";
import { goods } from "./mockStatic";
export default {
  name: "AReplenishment",
  components: {
    screen: () => import("@/components/BaseCom/screen/index.vue")
  },
  data() {
    return {
      value: ""
    };
  },
  mounted() {

  },
  methods: {
    filterData(fun) { // fun为过滤器方法名 返回值为数组[{label: '是', value: 1}]
      console.log(filterData(fun))
    },
    reset() {
      console.log("reset");
    },
    unrepeated() { // 数组对象去重
      const oldarr = [
        { id: 1, name: '小王' },
        { id: 2, name: '小李' },
        { id: 3, name: '小孙' },
        { id: 4, name: '小赵' },
        { id: 1, name: '小王' },
        { id: 5, name: '小钱' },
        { id: 3, name: '小孙' },
        { id: 2, name: '小李' },
        { id: 3, name: '小孙' },
      ]
      const newarr = unrepeated(oldarr, 'id')// oldarr 需要去重的原数组对象, 'id 去重数组的唯一码'
      console.log(newarr)// 显示的是去重后的数组
    },

    recursionFunction() { // 递归重构数据
      const oldarr = [{
        id: 1,
        name: "小王",
        child: [{ id: 2, name: "小李" }, { id: 3, name: "小前" }]
      }]
      // oldarr树形结构数据，
      // this.childRecursion做数据处理的方法 不需要(),
      // child 下级名称，不传就是children
      const newData = recursionFunction(oldarr, this.childRecursion, "child")
      console.log(newData)// 返回重构后的树形结构数据
    },
    childRecursion(obj) { // 递归的每一个方法都会调用childRecursion() 对每个对象进行操作 obj是当前递归到的对象
      return { id: obj.id + 1, name: obj.name, label: 1 }
    },
    antiShake() { // 防抖
      const time = 3000
      antiShake(time).then(res => { // time表示触发方法间隔时间
        console.log('我是' + time + 'ms内没有点击打印的内容')
      })
    },
    throttle() {
      const time = 500
      saveMobility(time).then(res => { // time表示每0.5秒调用一次
        console.log('我是每' + time + 'ms调用一次')
      })
    },
    createMockData() { // 生成mock命令
      console.log(goods)
      const oldobj = {
        id: "id",
        name: '名称',
        phone: "手机号",
        email: "邮箱",
        goodsName: goods.name, // 商品名称集合随机选取
        specs: goods.specs, // 商品规格集合随机选取
        model: goods.model, // 商品型号集合随机选取
        address: "地址",
        date: "日期",
        time: "时间",
        datetime: "日期时间",
        color: "颜色",
        number: "数量",
        price: "金额",
        type: "布尔",
        status: "状态,1,5", // 状态为标记 1是开始值 5是结束值  不传5 默认从零开始 1结束
        image: "图片",
        file: "文件",
        cparagraph: "段落",
        csentence: "句子",
        ctitle: "标题"
      }
      //       const aa = {
      //         id: "id",
      //       goodsName: goods.name, // 商品名称集合随机选取
      //         specs: goods.specs, // 商品规格集合随机选取
      //         model: goods.model, // 商品型号集合随机选取
      //         createDate: "日期时间",
      //         image: "图片",
      //  status: "状态,1", // 状态为标记 1是开始值 5是结束值  不传5 默认从零开始 1结束
      //       }
      const newobj = createMockData(oldobj)// oldobj 需要的数据获取的数据,key是需要绑定的参数 value是需要的类型
      // const newobj = createMockData(aa)// oldobj 需要的数据获取的数据,key是需要绑定的参数 value是需要的类型
      console.log(newobj)// 显示的是可以使用在mock里的对象
      this.saveJsonFile(newobj, 'mockData.json')// newobj是对象,mockData.json是文件名
    },
    saveJsonFile(obj, fileName) { // obj是对象,fileName是文件名
      saveJsonFile(obj, fileName)
    }

  }
};
</script>
<style  lang="scss" scoped>
</style>
