 * @Author: 徐强
 * @Date: 2021-08-11 14:03:27
-->
<template>
  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <tip-dialog v-model="reason" title="审核提示" dialog-type="input" tool-tips="输入框上的提示" :btn-param="btnParam" :visible.sync="inputVisible" @affirm="affirm" />
      <el-button @click="openDialog(1)">审核提示</el-button>
      <tip-dialog v-model="reason" title="删除提示" dialog-type="tips" tool-tips="删除前的警告" :btn-param="btnParam" :visible.sync="tipVisible" @affirm="affirm" />
      <el-button @click="openDialog(2)">删除提示</el-button>
      <batch-preview title="批量预览文件" :visible.sync="batchPreviewVisible" :file-list="fileList" />
      <el-button @click="openDialog(3)">批量预览弹框</el-button>
      <!-- <el-button @click="openDialog(2)">预览docx</el-button>
      <el-button @click="openDialog(3)">预览doc</el-button>
      <el-button @click="openDialog(4)">预览图片</el-button> -->
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "AReplenishment",
  components: {
    batchPreview: () => import('@/components/BaseCom/batchPreview/index'),
    tipDialog: () => import("@/components/BaseCom/tipDialog/index"),
  },
  data() {
    return {
      batchPreviewVisible: false,
      inputVisible: false,
      tipVisible: false, // 是否展示提示框
      reason: "", // 审核理由
      btnParam: [{ btnlable: '审核', type: 'danger' }, { btnlable: '修改', type: 'success' }], // 自定义按钮组
      fileList: [],
    };
  },
  mounted() {
  },
  methods: {
    openDialog(index) {
      if (index === 1) {
        this.inputVisible = true
      } else if (index === 2) {
        this.tipVisible = true
      } else if (index === 3) {
        this.batchPreviewVisible = true
        this.fileList = [
          { name: 'pdf文件', url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/0ff58062-0f82-476f-9d66-52d9e9bd7361.pdf' },
          { name: 'doc文件', url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/607241ca-3083-4105-8776-8f34eee61e6a.doc' },
          { name: '图片文件', url: 'https://itree.i-ortho.cn/api/upload/20220705/微信图片_20220704151224.jpg' }
        ]
      }
      // this.tipVisible = true;
    },
    affirm(type, value) {
      console.log(type, value)
    },
  }
};
</script>
<style  lang="scss" scoped>
.specialform {
  ::v-deep .formitem {
    margin-bottom: 0px;
  }
}
</style>
