import request from '@/utils/request'

export function getBtnIndex(data) { // 按钮列表接口
  return request({
    url: '/functionPage/menu/btnlist',
    method: 'post',
    data
  })
}
export function getMenuIndex(data) { // 菜单列表接口
  return request({
    url: '/functionPage/menu/index',
    method: 'post',
    data
  })
}
export function getRoleDetail(data) { // 菜单列表接口
  return request({
    url: '/functionPage/role/detail',
    method: 'post',
    data
  })
}

